	module.exports = {"data":{"markdownRemark":{"html":"<h3>Program Dashboard</h3>\n<p>We’ve revamped the Program Dashboard with new metric tables and charts that give better insight into reporting and analytics for programs.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/may_2019_program_dashboard-f73245ede20e6c45b15846f4f8c9234c-b74c4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 52.20125786163522%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"program dashboard\"\n        title=\"\"\n        src=\"/static/may_2019_program_dashboard-f73245ede20e6c45b15846f4f8c9234c-2f183.png\"\n        srcset=\"/static/may_2019_program_dashboard-f73245ede20e6c45b15846f4f8c9234c-67829.png 125w,\n/static/may_2019_program_dashboard-f73245ede20e6c45b15846f4f8c9234c-5c59f.png 250w,\n/static/may_2019_program_dashboard-f73245ede20e6c45b15846f4f8c9234c-2f183.png 500w,\n/static/may_2019_program_dashboard-f73245ede20e6c45b15846f4f8c9234c-c93e8.png 750w,\n/static/may_2019_program_dashboard-f73245ede20e6c45b15846f4f8c9234c-4e628.png 1000w,\n/static/may_2019_program_dashboard-f73245ede20e6c45b15846f4f8c9234c-b3ac4.png 1500w,\n/static/may_2019_program_dashboard-f73245ede20e6c45b15846f4f8c9234c-b74c4.png 2862w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Enhancements to the Jira Integration</h3>\n<p>We've added these improvements to the bi-directional Jira integration:</p>\n<h4>Additional Fields</h4>\n<p>The HackerOne to Jira escalation template now includes all additional fields that are either a type of <code>string</code>, <code>number</code>, or <code>date</code>. This enables Jira users to have all fields in Jira be mapped to a value from the HackerOne report. All available Jira fields will automatically be pulled from the selected issue type.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/may_2019_jira++_additional-fields-85a818a8d7f6161366b11c60964a4425-83d10.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 104.40835266821347%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"additional fields wen escalating HackerOne to Jira\"\n        title=\"\"\n        src=\"/static/may_2019_jira++_additional-fields-85a818a8d7f6161366b11c60964a4425-2f183.png\"\n        srcset=\"/static/may_2019_jira++_additional-fields-85a818a8d7f6161366b11c60964a4425-67829.png 125w,\n/static/may_2019_jira++_additional-fields-85a818a8d7f6161366b11c60964a4425-5c59f.png 250w,\n/static/may_2019_jira++_additional-fields-85a818a8d7f6161366b11c60964a4425-2f183.png 500w,\n/static/may_2019_jira++_additional-fields-85a818a8d7f6161366b11c60964a4425-c93e8.png 750w,\n/static/may_2019_jira++_additional-fields-85a818a8d7f6161366b11c60964a4425-4e628.png 1000w,\n/static/may_2019_jira++_additional-fields-85a818a8d7f6161366b11c60964a4425-83d10.png 1293w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h4>Sync Attachments</h4>\n<p>Jira users can now sync attachments from their HackerOne report to Jira by selecting <strong>Synchronize attachments</strong> in the <strong>Select HackerOne to Jira events</strong> section when configuring their Jira integration.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/may_2019_jira++_sync-attachments-55c02cfc807929a4039cf785522b56ac-f3a6d.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 69.53937592867756%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"synchronize attachments option in the jira integration\"\n        title=\"\"\n        src=\"/static/may_2019_jira++_sync-attachments-55c02cfc807929a4039cf785522b56ac-2f183.png\"\n        srcset=\"/static/may_2019_jira++_sync-attachments-55c02cfc807929a4039cf785522b56ac-67829.png 125w,\n/static/may_2019_jira++_sync-attachments-55c02cfc807929a4039cf785522b56ac-5c59f.png 250w,\n/static/may_2019_jira++_sync-attachments-55c02cfc807929a4039cf785522b56ac-2f183.png 500w,\n/static/may_2019_jira++_sync-attachments-55c02cfc807929a4039cf785522b56ac-c93e8.png 750w,\n/static/may_2019_jira++_sync-attachments-55c02cfc807929a4039cf785522b56ac-4e628.png 1000w,\n/static/may_2019_jira++_sync-attachments-55c02cfc807929a4039cf785522b56ac-f3a6d.png 1346w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h4>Automated Report Closure</h4>\n<p>Jira users can now select which Jira closed issue status should result in the closure of the HackerOne report.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/may_2019_jira++_report-closure-7f39ab3e6273436dee6c1820f13f80de-f3a6d.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 71.47102526002972%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"automating report closure with Jira\"\n        title=\"\"\n        src=\"/static/may_2019_jira++_report-closure-7f39ab3e6273436dee6c1820f13f80de-2f183.png\"\n        srcset=\"/static/may_2019_jira++_report-closure-7f39ab3e6273436dee6c1820f13f80de-67829.png 125w,\n/static/may_2019_jira++_report-closure-7f39ab3e6273436dee6c1820f13f80de-5c59f.png 250w,\n/static/may_2019_jira++_report-closure-7f39ab3e6273436dee6c1820f13f80de-2f183.png 500w,\n/static/may_2019_jira++_report-closure-7f39ab3e6273436dee6c1820f13f80de-c93e8.png 750w,\n/static/may_2019_jira++_report-closure-7f39ab3e6273436dee6c1820f13f80de-4e628.png 1000w,\n/static/may_2019_jira++_report-closure-7f39ab3e6273436dee6c1820f13f80de-f3a6d.png 1346w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h4>Severity to Priority Mapping</h4>\n<p>With severity to priority mapping, Jira users can map HackerOne severity ratings to the priority fields they have in Jira. This enables the right priority to be set when escalating a report to Jira.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/may_2019_jira++_severity-priority-mapping-e4107103316c781c7c218ca2a0745884-cd606.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 46.98412698412698%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"severity to priority mapping\"\n        title=\"\"\n        src=\"/static/may_2019_jira++_severity-priority-mapping-e4107103316c781c7c218ca2a0745884-2f183.png\"\n        srcset=\"/static/may_2019_jira++_severity-priority-mapping-e4107103316c781c7c218ca2a0745884-67829.png 125w,\n/static/may_2019_jira++_severity-priority-mapping-e4107103316c781c7c218ca2a0745884-5c59f.png 250w,\n/static/may_2019_jira++_severity-priority-mapping-e4107103316c781c7c218ca2a0745884-2f183.png 500w,\n/static/may_2019_jira++_severity-priority-mapping-e4107103316c781c7c218ca2a0745884-c93e8.png 750w,\n/static/may_2019_jira++_severity-priority-mapping-e4107103316c781c7c218ca2a0745884-4e628.png 1000w,\n/static/may_2019_jira++_severity-priority-mapping-e4107103316c781c7c218ca2a0745884-cd606.png 1260w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>","frontmatter":{"path":"/changelog/2019/may","title":"May 2019"}}},"pathContext":{}}

